<template>
  <div v-if="showPortal">
    <div class="portal">
      <div class="form-container">
        <form action="">
          <div class="close" @click="closePortal">
            <i class="fa fa-times"></i>
          </div>
          <h3>Login</h3>
          <h4 class="login-guide">Username = admin & Password = admin</h4>
          <input type="text" v-model="username" placeholder="username (admin)">
          <input type="password" v-model="password" placeholder="password (admin)">
          <button @click="login" class="submit-btn">Login</button>
        </form>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "LoginPortal",
  data: () => {
    return {
      showPortal: false,
      username: "",
      password: ""
    }
  },

  mounted() {
    this.$root.$on("loginClicked",  () => {
      this.showPortal = true;
    });
  },
  methods: {
    login: function (e) {
      e.preventDefault();

      if (this.username.toString().trim().toLowerCase() === "admin" && this.password.toString().trim().toLowerCase() === "admin") {
        this.$root.$emit("loggedIn");
      }

      this.showPortal = false;
    },
    closePortal() {
      this.showPortal = false;
    }
  },
}
</script>

<style scoped>
.portal {
  position: fixed;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  background: #000000de;
  z-index: 1000000000;
  justify-content: center;
  align-items: center;
  display: flex;
}

.portal form {
  background: #0f0f0f;
  box-shadow: 0 0 10px 0 rgb(255 255 255 / 60%);
  position: relative;
  border-radius: 1rem;
}

.portal .form-container {
  max-width: 50vw;
}

.portal .close {
  position: absolute;
  right: 50px;
  font-size: 17px;
  color: #fff;
  cursor: pointer;
  user-select: none;
}

.portal .login-guide {
  color: #fff;
  font-weight: lighter;
  font-size: 15px;
}
</style>