<template>
  <div class="works section-container" id="works">
    <h3 class="section-second-title">recent projects</h3>
    <h2 class="section-first-title">I love my job. These are <br> examples of my recent work</h2>
    <div class="search-works">
      <div class="form-container">
        <form action="">
          <h3 class="search-head">Search</h3>
          <input type="text" v-model="searchID" placeholder="Work ID">
          <button @click="doSearch" class="submit-btn">Search</button>
        </form>
      </div>
    </div>
    <div class="gallery">
      <section class="gallery-item" v-for="work in works" :key="work.id">
        <div class="overlay" @click="editPost" :data-id="work.id" :title="work.title">
          <i @click="deletePost" :data-id="work.id" v-if="loggedIn" class="fa fa-remove"></i>
          <i v-if="loggedIn" class="fa fa-edit"></i>
          <div class="caption">
            <h3>{{ work.captionHead }}</h3>
            <p>{{ work.captionDetail }}</p>
          </div>
        </div>
        <img :src="work.imgSrc" :alt="work.imgAlt">
      </section>
    </div>
    <div class="form-container" v-if="loggedIn && (updateMode === -1)">
      <form action="">
        <h3>Add New Work</h3>
        <input type="text" v-model="title" placeholder="title">
        <input type="text" v-model="detail" placeholder="detail">
        <input type="text" v-model="imageSrc" placeholder="image source">
        <button @click="doAdd" class="submit-btn">Add</button>
      </form>
    </div>

    <div class="form-container" v-if="updateMode !== -1">
      <form action="">
        <h3>Edit Work</h3>
        <input type="text" v-model="title" placeholder="title">
        <input type="text" v-model="detail" placeholder="detail">
        <input type="text" v-model="imageSrc" placeholder="image source">
        <button @click="doUpdate" class="submit-btn">Edit</button>
      </form>
    </div>
  </div>
</template>

<script>
import axios from "axios"

export default {
  name: "Works",
  data: () => {
    return {
      works: [],
      loggedIn: false,
      title: "",
      detail: "",
      imageSrc: "",
      updateMode: -1,
      searchID: ""
    }
  },
  mounted() {

    this.$root.$on("loggedIn", () => {
      this.loggedIn = true;
    });

    this.$root.$on("loggedOut", () => {
      this.loggedIn = false;
    })

    axios.get("https://my-json-server.typicode.com/mohammad-photo/demo/works")
        .then(({data}) => {
          data.forEach(work => {
            this.works.push({
              id: work.id,
              title: work.title,
              captionHead: work.title,
              captionDetail: work.info,
              imgSrc: work.img,
              imgAlt: work.title
            })
          })
        });
  },

  methods: {
    deletePost({target}) {
      if (!this.loggedIn)
        return false;

      const postID = +target.getAttribute("data-id");

      this.works = this.works.filter((work) => work.id !== postID);

      axios.delete("https://my-json-server.typicode.com/mohammad-photo/demo/works/" + postID);
    },
    doAdd(e) {
      e.preventDefault();

      if (!this.loggedIn)
        return false;

      if (this.title.trim().length === 0 || this.detail.trim().length === 0 || this.imageSrc.trim().length === 0)
        return false;

      const lastWorkID = this.works[this.works.length - 1].id;
      const {title, detail, imageSrc} = this;

      const newWork = {
        id: lastWorkID + 1,
        title,
        captionHead: title,
        captionDetail: detail,
        imgSrc: imageSrc,
        imgAlt: title
      };

      this.works.push(newWork)

      axios.post("https://my-json-server.typicode.com/mohammad-photo/demo/works", newWork);

      this.title = "";
      this.detail = "";
      this.imageSrc = "";


    },
    editPost({target}) {
      if (!this.loggedIn)
        return false;

      const postID = +target.getAttribute("data-id");
      const post = this.works.find(({id}) => id === postID);
      if (!post)
        return false;

      this.updateMode = post.id;
      this.title = post.title
      this.detail = post.captionDetail
      this.imageSrc = post.imgSrc
    },
    doUpdate(e) {
      e.preventDefault();

      const editedWork = {
        id: this.updateMode,
        title: this.title,
        captionHead: this.title,
        captionDetail: this.detail,
        imgSrc: this.imageSrc,
        imgAlt: this.title
      }

      this.works = this.works.map((work) => {
        if (work.id === this.updateMode)
          return editedWork;
        else
          return work;
      });

      axios.put("https://my-json-server.typicode.com/mohammad-photo/demo/works/" + this.updateMode, editedWork);

      this.updateMode = -1;
      this.title = "";
      this.detail = "";
      this.imageSrc = "";
    },
    doSearch(e) {
      e.preventDefault();

      if (this.searchID.trim().length === 0) {
        this.works = [];
        axios.get("https://my-json-server.typicode.com/mohammad-photo/demo/works")
            .then(({data}) => {
              data.forEach(work => {
                this.works.push({
                  id: work.id,
                  title: work.title,
                  captionHead: work.title,
                  captionDetail: work.info,
                  imgSrc: work.img,
                  imgAlt: work.title
                })
              })
            });
      } else {

        const search = +this.searchID;

        axios.get("https://my-json-server.typicode.com/mohammad-photo/demo/works/" + search)
            .then(({data}) => {
              console.log(data);
              this.works = [{
                id: data.id,
                title: data.title,
                captionHead: data.title,
                captionDetail: data.info,
                imgSrc: data.img,
                imgAlt: data.title
              }];
            }).catch(() => {
          return false;
        });
      }
    }
  }
}
</script>

<style scoped>
.search-works {
  margin-bottom: 150px;
}

.search-head {
  margin-bottom: 10px;
}
</style>