<template>
  <nav>
    <ul class="navigation-list">
      <li>
        <a href="#home">home</a>
      </li>
      <li v-if="showLogin">
        <a href="#" @click="doLogin">login</a>
      </li>
      <li v-if="!showLogin">
        <a href="#" @click="doLogout">log out</a>
      </li>
      <li>
        <a href="#istatistic">istatistic</a>
      </li>
      <li>
        <a href="#what-i-do">about</a>
      </li>
      <li>
        <a href="#works">works</a>
      </li>
      <li>
        <a href="#clients">clients</a>
      </li>
      <li>
        <a href="#contact-me">contact</a>
      </li>
    </ul>
  </nav>
</template>

<script>
export default {
  name: "NavigationList",
  data: () => {
    return {
      showLogin: true
    }
  },
  methods: {
    doLogin(e) {
      e.preventDefault();

      this.$root.$emit("loginClicked");
    },

    doLogout(e) {
      e.preventDefault();

      this.$root.$emit("loggedOut");
    }
  },
  mounted() {
    this.$root.$on("loggedIn", () => {
      this.showLogin = false;
    });

    this.$root.$on("loggedOut", () => {
      this.showLogin = true;
    })
  }
}
</script>

